body {
    margin: 0;
    padding: 0; 
}

div.MyStory {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    width: 100%; 
    height: 800px; 
    -ms-flex-direction: column; 
        flex-direction: column; 
    -ms-flex-pack: center; 
        justify-content: center;
    -ms-flex-align: center;
        align-items: center; 
    -ms-flex-line-pack: center; 
        align-content: center; 
    background: black; 
    background-image: url(../Resources/Images/sitting-on-a-bus.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: fill;
    -o-object-fit: fill;
       object-fit: fill;
    -webkit-filter: blur();
            filter: blur(); 
}

h1.MyStory {
    color: white;  
    border: 4px solid white;
    border-radius: 12px; 
}

button.MyStory {
    border-radius: 5px;
    color: white;   
    background: -webkit-gradient( linear, left top, left bottom, from(rgb(8, 76, 99)), to(rgb(124, 124, 194)));   
    background: -webkit-linear-gradient( rgb(8, 76, 99), rgb(124, 124, 194));   
    background: -o-linear-gradient( rgb(8, 76, 99), rgb(124, 124, 194));   
    background: linear-gradient( rgb(8, 76, 99), rgb(124, 124, 194)); 
}

p.MyStory {
    text-align: center; 
    font-family: sans-serif;
    border: 1px solid white;
    border-radius: 3px; 
    margin-left: 10%;
    margin-right: 10%; 
    
    color: white; 
}


